/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
body{
  background-color: lightblue;
  color: white;
}
.container{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.show{
  width: 250px;
  height: 25px;
  border-radius: 5px;
  font-size: 16px;
  position: relative;
}
.box{
  margin: 10px 0px;
  position: relative;
  height: 20px;
}
label{
  cursor: pointer;
  position: absolute;
  left: 0;
  font-size: 20px;
}
input {
  outline: 0px;
  border: 0px;
  width: 30px;
  position: absolute;
  right: 0;
}
input[type=checkbox]{
cursor: pointer;
}
button {
  border: 0px;
  color: lightblue;
  background-color: white;
  width: 120px;
  height: 35px;
  border-radius: 10px;
  font-size: 16px;
  cursor: pointer;
  font-weight: bold;
}
button:hover {
  box-shadow: 0 0px 15px 3px white;
}
